h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6
	font-weight: normal
	font-family: $s-m
	&:last-child
		margin-bottom: 0
h1, .h1
	+fSize(38px, 80px, $xs, $xl)
	line-height: 1
	margin-bottom: 20px
h2, .h2
	+fSize(38px, 63px, $sm, $xl)
	margin-bottom: 20px
	line-height: 1
	+sm
		+fSize(32px, 40px, $xs, $sm)

h3, .h3
	+fSize(26px, 36px, $sm, $xl)
	margin-bottom: 10px
	line-height: 1
	+sm
		+fSize(26px, 30px, $xs, $sm)

h4, .h4
	+fSize(21px, 24px, $sm, $xl)
	margin-bottom: 5px
	line-height: 1.2

h5, .h5
	+fSize(20px, 22px, $xs, $xl)
	margin-bottom: 15px
	line-height: 1.35

a
	color: currentColor
	text-decoration: none
	transition: $tr

p
	line-height: 1.36
	color: rgba(255, 255, 255, .8)
	margin-bottom: 20px
	&:last-child
		margin-bottom: 0

b
	font-weight: normal
	font-family: $s-m

a:not([class])
	color: currentColor
	text-decoration: 0
	border-bottom: 1px solid transparent
	padding-bottom: .08em
	&:hover
		color: $primary
		border-bottom: 1px solid $primary

.link-lg
	@extend h4
	padding-bottom: 5px
	transition: $tr
	&:hover
		border-color: $primary

ul:not([class])
	padding: 0
	margin-bottom: 20px
	&:last-child
		margin-bottom: 0
	li
		padding-left: 20px
		margin-bottom: 10px
		position: relative
		&:last-child
			margin-bottom: 0
		&:before
			content: ''
			width: 7px
			height: 7px
			border-radius: 50%
			border: 1px solid currentColor
			position: absolute
			left: 0
			top: 0.55em
		ul, ol
			margin-top: 10px

ol:not([class])
	counter-reset: listItem
	margin-bottom: 20px
	&:last-child
		margin-bottom: 0
	li
		margin-bottom: 10px
		position: relative
		&:last-child
			margin-bottom: 0
		&:before
			counter-increment: listItem
			content: counter(listItem)'. '
			color: currentColor
			margin-right: 5px
		ol
			li:before
				content: counters(listItem, '.') ' '
		ul, ol
			margin-top: 10px
			padding-left: 20px

.descr
	+fSize(15px, 16px, $xs, $xl)
	line-height: 1.3
	color: rgba(255, 255, 255, .55)
	a
		color: #fff