*, *:before, *:after
	box-sizing: border-box
	min-height: 0.001vw

html
	overflow: hidden
	&.overflow
		overflow: hidden
		body
			overflow: hidden
	&.active
		overflow: initial


body
	background: $bg
	color: $text
	font-family: $s-r, Arial, sans-serif
	width: 100%
	height: 100%
	+fSize(14px, 16px, $xs, $lg)
	line-height: 1.5
	min-height: 100vh
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale
	&.mobile
		.preloader
			display: none
		.letter-wrap
			&:hover, &:active
				color: $primary

img, svg
	max-width: 100%
	max-height: 100%
	display: inline-block
	vertical-align: top

textarea
	-webkit-appearance: none
	border-radius: 0

input
	-webkit-appearance: none
	border-radius: 0
	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button
		-webkit-appearance: none
		margin: 0

.main
	flex-grow: 1
	position: relative

.container
	max-width: 1440px
	width: 100%
	margin: 0 auto
	padding: 0 45px
	+md
		padding: 0 30px
	+xs
		padding: 0 20px

.icon
	transition: $tr

.title-animation
	opacity: 0
	&.active
		opacity: 1
		.letter-inner
			animation: blurMe 1s ease-in-out forwards
	.word
		display: inline-flex
		margin-right: 0.3em
		&:last-child
			margin-right: 0
	.letter
		overflow: hidden
		display: inline-flex
		&-inner
			display: inline-flex
			opacity: 0

@keyframes blurMe
	0%
		filter: blur(10px)
		opacity: 0
	100%
		filter: blur(0px)
		opacity: 1

.fade-animation, .fade-animation-bottom
	opacity: 0
	transition: .8s ease-in-out
	&.active
		opacity: 1
.fade-animation-bottom
	transform: translateY(50px)
	&.active
		transform: translateY(0)

.cursor-bg
	position: absolute
	z-index: 2