.btn
	display: inline-flex
	align-items: center
	justify-content: center
	text-align: center
	padding: 13px 27px
	border: 1px solid rgba(255, 255, 255, .2)
	border-radius: 12px
	font-size: 14px
	font-family: $s-m
	position: relative
	cursor: pointer
	transition: $ltr
	&:hover
		color: $primary
		border-color: $primary