.fp
	&-enabled
		.header
			opacity: 1
			transform: translateY(0)
		.social
			opacity: 1
			transform: translateY(0)
		#fp-nav ul
			opacity: 1
#fp-nav
	right: 50px!important
	+sm
		display: none
	ul
		opacity: 0
		transition: $ltr
		li
			width: 10px
			height: 10px
			margin: 0 0 8px
			padding: 0
			&:after, &:before
				content: none
			&:last-child
				margin-bottom: 0
		a
			position: relative
			display: block
			border-radius: 50%
			border: 1px solid rgba(255, 255, 255, .3)
			transition: $ltr
			&:hover
				border-color: #fff
			&.active
				border-color: #fff
				&:after
					transform: scale(1)
			span
				display: none
			&:after
				content: ''
				width: 100%
				height: 100%
				position: absolute
				top: 0
				bottom: 0
				left: 0
				right: 0
				border-radius: 50%
				background: #fff
				transform: scale(0)
				transition: $ltr