.section
	$root: &
	position: relative
	&--contacts
		.section-video
			width: 120%
			height: 120%
			left: -10%
			top: -10%
			video
				object-fit: contain
	&-video
		position: absolute
		z-index: 1
		top: 0
		bottom: 0
		left: 0
		right: 0
		video
			+sm
				display: none
		&:after
			content: ''
			position: absolute
			z-index: 2
			top: 0
			bottom: 0
			left: 0
			right: 0
			width: 100%
			height: 100%
			background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%)
			+sm
				background: linear-gradient(0deg, #000000 25%, rgba(0, 0, 0, 0) 100%)
		video
			object-position: center
			object-fit: cover
			width: 100%
			height: 100%
	&-container
		position: relative
		z-index: 3
		padding: 140px 80px 120px
		min-height: 100%
		display: flex
		overflow: hidden
		+lg
			padding: 120px 80px 100px
		+md
			padding: 100px 80px 100px 30px
		+sm
			padding-right: 40px
		+xs
			padding: 100px 20px clamp(60px, 8vw, 100px)
	&-row
		width: 100%
		display: flex
		justify-content: space-between
		+sm
			flex-direction: column
			justify-content: center
			align-items: center
		&.column
			flex-direction: column-reverse
			justify-content: end
			#{$root}-col
				align-self: unset

				&:first-child
					display: flex
					justify-content: space-between
					width: 100%
					align-items: center

				&:last-child
					display: flex
					flex: 1
					padding: 0
					margin: 0
					width: 100%
	&-reachout
		+sm
			text-align: right
		.text
			color: $secondary
			font-size: 16px
			line-height: 20px
		.link
			color: $text

	&-col
		width: 50%
		align-self: flex-end

			
		+lg
			width: 60%
		+sm
			width: 100%
			align-self: flex-start
		&:last-child
			width: 41%
			+lg
				width: 40%
				padding-left: 30px
			+sm
				width: 100%
				padding: 0
				margin-top: 30px
	&-block
		margin-bottom: 40px
		&:last-child
			margin-bottom: 0
	&-text
		margin-bottom: 25px
		&:last-child
			margin-bottom: 0


.portfolio
	display: flex
	justify-content: center
	align-items: center
	column-gap: 5px
	flex: 1

	+sm
		flex-direction: column
		justify-content: flex-start
		row-gap: 1rem

	&__inner
		position: relative
		display: flex
		flex-direction: column
		justify-content: center
		width: 100%
		height: 100%
		border: 1px solid transparent
		border-radius: 20px
		transition: border-color 0.3s 0s
		flex: 1
		

		.logo
			transition: 0.3s
			img
				display: block
				margin: 0 auto
				&.premia
					width: 143px

		.description
			position: absolute
			top: 50%
			transition: 0.3s
			transform: translateY(10px)
			opacity: 0
			text-align: center
			margin-top: 14px
			margin-bottom: 6px
			color: $secondary
			font-weight: 400
			font-size: 16px
			line-height: 20px
			overflow: hidden
			padding: 0 30px

		
		&:hover
			border: 1px solid rgba(255, 255, 255, 0.19)
			.description
				opacity: 1
				transform: translateY(0px)
			.logo
				transform: translateY(-20px)
		+sm
			border: 1px solid rgba(255, 255, 255, 0.19)
			.description
				opacity: 1
				transform: translateY(0px)
			.logo
				transform: translateY(-20px)
		

	&__item
		flex: 1
		height: 257px
		width: 100%
		+sm
			max-height: 190px


.careers
	&-content
		max-width: 412px
	&-logos
		display: flex
		align-items: center
		justify-content: space-between
		flex-wrap: wrap
		margin: -10px 0
		img
			margin: 10px 0
			&:nth-child(1)
				width: 28%
			&:nth-child(2)
				width: 19%
			&:nth-child(3)
				width: 19.5%
			&:nth-child(4)
				width: 12%
			&:nth-child(5)
				width: 28%
			&:nth-child(6)
				width: 17%
			&:nth-child(7)
				width: 22%
			&:nth-child(8)
				width: 9%



.contacts
	display: flex
	flex-direction: column
	justify-content: flex-end
	width: 100%
	text-align: right
	padding-bottom: 30px
	+sm
		justify-content: center
		text-align: left
	&-text
		max-width: 170px
		margin: 0 0 0 auto
		+sm
			margin: 0