.header
	position: fixed
	z-index: 11
	top: 0
	left: 0
	right: 0
	opacity: 0
	transform: translateY(-100%)
	transition: $ltr
	&-container
		height: 80px
		width: 100%
		margin: 0 auto
		display: flex
		align-items: center
		justify-content: space-between
		+sm
			height: 70px
	&-drop
		+sm
			position: fixed
			top: 0
			left: 0
			right: 0
			width: 100%
			height: 100vh
			background-color: $bg
			padding: 100px 20px 40px
			overflow-y: auto
			-webkit-overflow-scrolling: touch
			opacity: 0
			visibility: hidden
			z-index: -1
			pointer-events: none
			transition: $ltr
			&.active
				pointer-events: all
				opacity: 1
				visibility: visible
				z-index: 2
		&__inner
			display: flex
			align-items: center
			+sm
				display: block
		&__item
			margin-right: 34px
			&:last-child
				margin: 0
				+sm
					width: 100%
			+sm
				margin: 0 0 20px
				.btn
					width: 100%
	&-logo
		width: 143px
		flex: 0 0 143px
		position: relative
		z-index: 3
	&-menu
		display: flex
		margin: 0 -17px
		align-items: center
		.contact-us
			margin-left: 12px
		+sm
			display: block
			margin: 0
			.contact-us
				margin: auto
				margin-top: 12px
		&__item
			margin: 0 17px
			+sm
				display: block
				padding: 5px 0
				margin: 0
				text-align: center
		&__link
			color: rgba(255, 255, 255, .55)
			+sm
				font-size: 18px
		
	&-btn
		display: none
		align-items: center
		justify-content: center
		width: 24px
		height: 24px
		cursor: pointer
		margin-left: auto
		position: relative
		z-index: 3
		+sm
			display: flex
		&__inner
			width: 18px
			height: 12px
			position: relative
			span
				position: absolute
				left: 0
				right: 0
				width: 100%
				height: 2px
				background-color: #fff
				transition: $tr
				&:nth-child(1)
					top: 0
				&:nth-child(2)
					top: 0
					bottom: 0
					margin: auto
				&:nth-child(3)
					bottom: 0
		&.active
			span
				&:nth-child(1)
					transform: rotate(-45deg) translate(-5px, 5px)
				&:nth-child(2)
					transform: translateX(-5px)
					opacity: 0
				&:nth-child(3)
					transform: rotate(45deg) translate(-2px, -2px)