.cursor-title
	cursor: none
	display: inline-block
.cursor
	position: fixed
	z-index: 111
	transform: translate(-50%, -50%)
	border: 1px solid $primary
	padding: 1rem
	border-radius: 50%
	pointer-events: none
	opacity: 0
	transition: opacity .25s ease
	&.active
		opacity: .5
		animation: blinkCursor .75s ease infinite

.inner-cursor
	position: fixed
	z-index: 111
	transform: translate(-50%, -50%)
	background-color: #fff
	padding: 1rem
	opacity: 0
	border-radius: 50%
	pointer-events: none
	mix-blend-mode: difference
	transform-origin: center
	transition: opacity .25s ease
	&.active
		opacity: 1
		transform: translate(-50%, -50%) scale(1.5)

@keyframes blinkCursor
	0%
		transform: translate(-50%, -50%) scale(1.5)
	50%
		transform: translate(-50%, -50%) scale(2.5)
	100%
		transform: translate(-50%, -50%) scale(1.5)