.social
	position: fixed
	z-index: 11
	bottom: 0
	left: 0
	right: 0
	width: 100%
	height: 80px
	padding: 30px 0
	transform: translateY(100%)
	transition: $ltr
	&-list
		display: flex
		align-items: center
		margin: -7px
		+sm
			justify-content: flex-end
	&-item
		flex: 0 0 16px
		margin: 7px
	&-link
		width: 16px
		height: 16px
		display: flex
		align-items: center
		&:hover
			svg
				fill: $primary
		svg
			fill: rgba(255, 255, 255, .5)
