.underline-animation
    display: inline-block
    transition: 0.3s

    &:hover
        color: $primary

    &:after
        content: ""
        position: relative
        width: 0
        height: 1px
        display: block
        z-index: 100
        background-color: $primary
        transition: .3s ease-out all
        left: 50%
        top: 5px
        transform: translateX(-50%)

    &:hover:after
        width: 80%
	

			