.button
    display: flex
    align-items: center
    justify-content: center
    cursor: pointer
    border: 1px solid rgba(255, 255, 255, 0.21)
    border-radius: 12px
    width: 123px
    height: 46px
    color: #ffffff
    background: transparent
    font-weight: 600
    font-size: 14px
    line-height: 20px
    transition: 0.3s ease-out

    &:hover
        border-color: #ffffff